import React, { useState } from 'react'
import Modal from 'react-modal'
import css from './modalComment.module.css'
import Button from 'react-bootstrap/Button'
import * as api from '../api/apiService';

Modal.setAppElement('#root');

export default function ModalComment({handleCloseCommentClick}) {
  const [comments, setComments] = useState('')

  const handleClickClose = () => {
    handleCloseCommentClick()
  }

  const handleComments = (event) => {
    setComments(event.target.value)
  }

  const handlePublicar = async () => {
    const comentario =  {
      descricao: comments,
      FkProblema: 1,
      FkUsuario: null,
      datacomentario: null,
      horacomentario: null
    }

    const insertComentario = await api.insertComentario(comentario)
  }

  return (
    <div >
      <Modal isOpen={true} className={css.container_modal}>
        <div className={css.row}> 
        <span>Comentarios</span> <a onClick={handleClickClose} className={css.close}>X</a>
        </div>
        <div className={`${css.comments} form-group col-lg-15`}>
          <textarea
            className="form-control"
            id="comments"
            rows="3"
            value={comments}
            onChange={handleComments}
          ></textarea>
          <Button className={css.btn_publicar} onClick={handlePublicar}>Publicar</Button>
          </div>
      </Modal>
    </div>
  )
}
